import React, { useState, useEffect } from 'react';
import { View, Text, StyleSheet } from 'react-native';
import axios from 'axios';

const ParkingTab = ({ tabName }) => {
  const [slots, setSlots] = useState([false, false, false]); // Estado inicial de los slots
  useEffect(() => {
    const fetchSensorData = async () => {
      try {
        const response = await axios.get('http://192.168.1.15/api/sensores');
        setSlots(response.data.slots);
      } catch (error) {
        console.error('Error al obtener los datos de los sensores:', error);
      }
    };

    fetchSensorData();
  }, []);
  // Aquí deberías agregar la lógica para actualizar los estados basado en los datos recibidos de tu backend

  return (
    <View style={styles.container}>
      <Text style={styles.title}>{tabName}</Text>
      {slots.map((occupied, index) => (
        <View key={index} style={[styles.led, occupied ? styles.ledOn : styles.ledOff]} />
      ))}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  title: {
    fontSize: 24,
    marginBottom: 20,
  },
  led: {
    width: 50,
    height: 50,
    borderRadius: 25,
    margin: 10,
  },
  ledOn: {
    backgroundColor: 'red',
  },
  ledOff: {
    backgroundColor: 'green',
  },
});

export default ParkingTab;
